import { LinkProps, Theme, alpha } from '@mui/material';
import React, { useEffect, useState } from 'react';

import JsxParser from 'react-jsx-parser';
import LinkParser from './LinkParser';

const DEFAULT_LINK_PROPS: LinkProps = {
  target: '_blank',
};

export interface ITextParserProps {
  children: string;
  linkProps?: LinkProps;
}

const TextParser = ({
  children,
  linkProps = DEFAULT_LINK_PROPS,
}: ITextParserProps) => {
  const [previewText, setPreviewText] = useState<string>(children);

  useEffect(() => {
    if (children) {
      const finalOutput = children.replace(
        /(\b(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&//=]*))/gim,
        '<LinkParser {...linkProps} href="$1">$1</LinkParser>',
      );
      setPreviewText(finalOutput);
    }
  }, [children]);

  return (
    <>
      <JsxParser
        bindings={{
          linkProps: linkProps,
        }}
        components={{ LinkParser }}
        jsx={previewText}
        renderInWrapper={false}
        autoCloseVoidElements={true}
      />
    </>
  );
};

export default TextParser;
