import { Box, Link, Typography } from '@mui/material';

import { IJob } from '../../../data/resume/job';
import React from 'react';
import StyledLink from '../../StyledLink';
import StyledTypography from '../../StyledTypography';
import TextParser from '../../Parser/TextParser';

const Job = ({ company, position, link, dateRange, points }: IJob) => {
  return (
    <>
      <Box>
        <StyledLink
          to={link}
          label={company}
          target="_blank"
          bottomBorder={false}>
          <StyledTypography fontVariant="titleLight" capitalized={true}>
            {`${company} - ${position}`.toUpperCase()}
          </StyledTypography>
        </StyledLink>
        <StyledTypography
          fontVariant="subtitle"
          variant="overline"
          aria-label="subtitle"
          capitalized={true}>
          {dateRange}
        </StyledTypography>
        <ul
          style={{
            paddingInlineStart: 16,
          }}>
          {points.map((point, index) => (
            <li key={`${company}-point-${index}`}>
              <Typography component="span">
                <TextParser>{point}</TextParser>
              </Typography>
            </li>
          ))}
        </ul>
      </Box>
    </>
  );
};

export default Job;
