import { Link, LinkProps } from '@mui/material';
import React, { useState } from 'react';

import StyledLink from '../StyledLink';
import { useEffect } from 'react';

export interface ILinkParserProps extends LinkProps {}

const LinkParser = ({ href, ...rest }: ILinkParserProps) => {
  const [link, setLink] = useState(href ?? '/');

  useEffect(() => {
    if (href) {
      const properLink = href.startsWith('http') ? href : `http://${href}`;
      setLink(properLink);
    }
  }, [href]);

  return <StyledLink to={link} label={link} />;
};

export default LinkParser;
