import { Box, Stack } from '@mui/material';

import Certifications from '../components/Resume/Certifications';
import Education from '../components/Resume/Education';
import Experience from '../components/Resume/Experience';
import PageContentLayout from '../layout/PageContentLayout';
import PageLayout from '../layout/PageLayout';
import React from 'react';
import { ReactNode } from 'react';
import References from '../components/Resume/References';
import Skills from '../components/Resume/Skills';
import StyledTypography from '../components/StyledTypography';
import Summary from '../components/Resume/Summary';

interface Section {
  title: string;
  details: ReactNode;
}
const sections: Section[] = [
  {
    title: 'Summary',
    details: <Summary />,
  },
  {
    title: 'Skills',
    details: <Skills />,
  },
  {
    title: 'Experience',
    details: <Experience />,
  },
  {
    title: 'Certifications',
    details: <Certifications />,
  },
  {
    title: 'Education',
    details: <Education />,
  },
  {
    title: 'References',
    details: <References />,
  },
];

const scrollTo = (elementId: string) => {
  const clientTopPosition = document
    ?.getElementById(elementId.toLowerCase())
    ?.getBoundingClientRect()?.top;
  if (clientTopPosition) {
    window.scrollTo({
      top: clientTopPosition - 86,
      behavior: 'smooth',
    });
  }
};

const ResumeNavigation = () => {
  return (
    <Stack
      direction="row"
      justifyContent="center"
      sx={{
        flexWrap: 'wrap',
      }}>
      {sections.map((section: Section) => (
        <StyledTypography
          key={section.title}
          bold={true}
          capitalized={true}
          hoverEffect={true}
          variant="overline"
          mr={2}
          onClick={() => {
            scrollTo(section.title);
          }}>
          {section.title}
        </StyledTypography>
      ))}
    </Stack>
  );
};

interface IResumeProps {}

const Resume = ({}: IResumeProps) => {
  const pageName = 'Resume';
  return (
    <>
      <PageLayout title={pageName} description={pageName}>
        <PageContentLayout
          header={pageName}
          headerProps={{ textAlign: 'center' }}
          subHeader={<ResumeNavigation />}>
          <Stack spacing={2}>
            {sections.map((section: Section) => (
              <Box
                key={section.title}
                display="flex"
                pb={2}
                id={section.title.toLowerCase()}>
                {section.details}
              </Box>
            ))}
          </Stack>
        </PageContentLayout>
      </PageLayout>
    </>
  );
};

export default Resume;
